*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: var(--theme-contrast-color);
}

:root
{
    --theme-primary-color: #EDF0F0;
    --theme-secondary-color: #ffffff;
    --theme-contrast-color: #000000;
    --theme-substitue-color: #c3c6d1;
    --theme-contrast-dull: #2a2b2b;
    --hero-font: "quicksand";
    --content-font: "cantarell";
    --font-resizer: calc(0.5rem + 0.6vw + 0.2vh);
    --blend-color: #c3c6d113;
}

body{
    background-color: var(--theme-primary-color);
}

img
{
    max-width: 100%;
    height: auto;
    width: auto\\9; /* ie8 */
}

#main-header
{
    /* height: 50px; */
    /* height: 80px; */
    height: clamp(40px, 10vh, 80px);
    width: 100vw;
    background-color: var(--theme-secondary-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* padding: 0 clamp(30px, 10%,100px); */
    padding: calc(0.5rem + var(--font-resizer));
    box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.11);
 
}

.logo
{
    display: flex;
    height: 100%;
    width: fit-content;
    justify-content: center;
    align-items: center;
    font-size: calc(0.8rem + 1.4vw + 0.3vh);
    font-family: var(--hero-font), 'Times New Roman', Times, serif, sans-serif;
}
.n-logo
{
   height: calc(3.99rem + 0.9vw + 0.6vh);
   min-height: 1rem;
   max-height: 100px;
   width: auto;
}
/* No avatar element in the page yet */
.avatar
{
    height: calc(1.5rem + 0.6vw + 0.2vh);
    width: calc(1.5rem + 0.6vw + 0.2vh);
    display: inline-block;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: var(--theme-secondary-color);
}

.corner-profile
{
    /* background-color: rgba(137, 43, 226, 0.267); */
    display: flex;
    list-style-type: none;
    gap: 0.5em;
}

.corner-profile .info-btn
{
    background-color: transparent;
    border: none;
    padding: auto;
}
.corner-profile .info-btn i
{
    font-size: calc(1.3rem + 0.6vw + 0.2vh);
}

.corner-profile li
{
    padding: 0 1vw;
}

/* Main styling */

#container
{
    font-family: var(--content-font), sans-serif;
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 0fr auto;
    height: calc(100vh - (clamp(40px, 10vh, 80px) + clamp(20px, 5vh, 40px)));
    overflow: auto;
}

/* Nav bar */
#navbar-container
{
    grid-column: 2/5;
    grid-row: 1/1;
}
#list-navigator
{
    height: clamp(20px, 5vh, 60px);
    width: 100%;
    /* background-color: aliceblue; */
    box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.11);
    margin-top: 2px
}

#burger-icon
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    height: 100%;
    width: 100%;
    min-width: 2rem;
    max-width: 100px;
    /* background-color: black; */
}

#burger-icon .line
{
    background-color: var(--theme-contrast-color);
    width: 80%;
    height: 3px;   
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    margin: 2px 0;
}
#burger-icon .line1
{
    width: 80%;
}#burger-icon .line2
{
    width: 60%;
}#burger-icon .line3
{
    width: 40%;
}


#list-navigator ul
{
    list-style-type: none;
    display: flex;
    gap: max(3px, 0.3vw);
    justify-content: flex-start;
    align-items: center;
    height: 100%;
    /* overflow-x: scroll; */
    overflow-x: auto;
    /* scrollbar-width: none; */
}
/* #list-navigator ul::-webkit-scrollbar
{
    display: none;
} */
#list-navigator ul li>button
{
    background-color: transparent;
    border: none;
    font-size: calc(0.5rem + 0.6vw + 0.2vh);
    height: 100%;
    width: 100%;

}
#list-navigator ul li
{
    position: relative;
    height: 100%;
    width: max-content;
    max-width: fit-content;
    min-width: fit-content;
    padding: 0 3%;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
}
#list-navigator ul li::after
{
    content: " ";
    height: 60%;
    width: 2px;
    background-color: var(--theme-contrast-color);
    position: absolute;
    top: 20%;
    right: 0;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    opacity: 0.4;
}
#list-navigator ul li:last-child
{
    opacity: 0.3;
}
#list-navigator ul li:hover
{
    background-color: var(--theme-substitue-color);
    transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -ms-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
}

/* aside-section */
#side-column
{
    /*grid-area: side-menu;
    */background-color: var(--theme-primary-color);
    grid-column: 1/2;
    grid-row: 1/5;
    padding: calc(0.5rem + var(--font-resizer));
    box-shadow: 7px 2px 3px rgba(0, 0, 0, 0.11);
    z-index: 2;
    overflow-y: auto;
}
.general-heading
{
    margin: max(2px, 0.3vw) max(4px, 2vw);
    font-size: calc(0.6rem + var(--font-resizer));
}
#all-projects
{
    list-style-type: none;
    display: flex;
    flex-direction: column;
    gap: 4px;
    justify-content: flex-start;
    align-items: center;
    /* border: 1px solid salmon; */
    margin-top: 10%;
    font-weight: bolder;
    padding: calc(var(--font-resizer));

}

#all-projects li
{
    min-width: fit-content;
    width: max(100px, 80%);
    background-color: var(--theme-contrast-dull);
    height: clamp(20px, 5vh, 60px);
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 4px;
    padding: calc(var(--font-resizer));
}

#all-projects li button
{
    min-width: fit-content;
    width: 100%;
    background-color: transparent;
    border: none;
    font-size: calc(0.2rem + var(--font-resizer));
    min-height: 100%;
    color: var(--theme-secondary-color);
}
.fa-trash
{
    /*content: "X";
    */color: salmon;
    font-size: var(--font-resizer);
    
    margin-right: 3%;
    opacity: 1;
    background-color: #c3c6d113;
    padding: clamp(3px, 4%, 6px);
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
}
button[del-list-index], button[del-project-index]
{
    border: none;
}
button[del-list-index]:hover
{
    background-color: var(--theme-contrast-dull);
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
}
#all-projects li:hover .fa-trash
{
    background-color: var(--theme-contrast-color);
    opacity: 1;
}

#all-projects li:last-child
{
    opacity: 0.3;
}
#all-projects li:hover
{
    background-color: var(--theme-contrast-color);
    transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
}


/* list area */

#main-area
{
    /* grid-area: main; */
    background-color: var(--theme-secondary-color);
    grid-column: 2/5;
    grid-row: 2/5;
}

#main-lists-area
{
    padding: max(5px, 1.7vw);
}

#main-lists-area hr
{
    background-color: var(--theme-contrast-dull);
    height: 1px;
    width: 60%;
    border-radius: 2px;
    border: none;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
    opacity: 0.6;
}

.date-and-time
{
    width: 50vw;
}
.date-and-time #date, .date-and-time #time
{
    margin: 0 5%;
    font-size: var(--font-resizer);
}

#list-container
{
    height: fit-content;
    width: fit-content;
    max-width: 100%;
    min-width: clamp(100px, 45%, 250px);
    border: 1px outset var(--theme-contrast-color);
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    margin: 3% 0;
    position: relative;
    padding: max(20px, 3%) 3%;
    box-shadow: 3px 3px var(--theme-contrast-dull);
}
#list-container::before
{
    content: "....";
    font-family: monospace;
    background-color: var(--theme-contrast-dull);
    color: var(--theme-substitue-color);
    border-radius: 3px 3px 0 0 ;
    -webkit-border-radius: 3px 3px 0 0 ;
    -moz-border-radius: 3px 3px 0 0 ;
    -ms-border-radius: 3px 3px 0 0 ;
    -o-border-radius: 3px 3px 0 0 ;
    width: 100%;
    position: absolute;
    text-align: center;
    height: fit-content;
    top: 0;
    left: 0;
   right: 0;
}

#list dt
{
    font-weight: bold;
    position: relative;
    font-size: calc(0.2rem + var(--font-resizer));
    padding-left: calc(clamp(10px, 2vw, 15px)  + 0.5rem);
    width: 90%;
    word-wrap: break-word;
}
#list dt span
{
    height: clamp(10px, 2vw, 15px);
    width: clamp(10px, 2vw, 15px);
    border: 2px solid;
    
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: calc(clamp(10px, 2vw, 15px)/2);
}
#list dt span:hover
{
    background-color: var(--theme-substitue-color);
    background-color: var(--priority-color);
}
#list dd
{
    margin: 3% clamp(30px, 5vw, 40px);
    position: relative;
    padding-left: calc(clamp(10px, 2vw, 15px)  + 0.2rem);
     font-size: calc(0.04rem + var(--font-resizer));
    width: 80%;
    word-wrap: break-word;
    min-height: clamp(10px, 3vh, 50px);
    display: flex;
    flex-direction: column;
}

#list dd::before
{
    content: " ";
    height: clamp(5px, 1.2vw, 10px);
    width: clamp(5px, 1.2vw, 10px);
    background-color: var(--theme-contrast-color);
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: calc(clamp(10px, 2vw, 15px)/2);
    
}

#list dd span:last-child
{
    font-size: calc(var(--font-resizer) - 0.2em);
    font-family: var(--hero-font);
    position: relative;
    margin-top: 10%;
    right: -70%;
    bottom: 0;
}

#add-task
{
    opacity: 0.5;
    border: 1px solid rgba(0, 0, 0, 0.418);
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    text-align: center;
    padding: 0;
    margin-top: 5vh;
  
}

#add-task:hover
{
    background-color: var(--theme-substitue-color);
}
/* footer */

#universal-footer
{
    /* grid-area: footer; */
    background-color: var(--theme-contrast-color);
    box-shadow: 0px -4px 4px rgba(0, 0, 0, 0.404);
    z-index: 4;
    color: var(--theme-substitue-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.2rem;
    height: clamp(20px, 5vh, 40px);

    /* position: relative; */
}
#universal-footer pre a{
    color: var(--theme-substitue-color);
}

#universal-footer pre {
    display: flex;
    color: var(--theme-substitue-color);
    align-items: center;
}

#universal-footer .fa-github
{
    color: var(--theme-substitue-color);
