iCAAiC;IACjC,kBAAkB;IAClB,oDAAoD;KACnD,8CAA8C;IAC/C,UAAU;IACV,qBAAqB;IACrB,kCAAkC;IAClC,aAAa;IACb,sBAAsB;AAC1B;;AAEA;;IAEI,YAAY;IACZ,+BAA+B;IAC/B,8BAA8B;IAC9B,6CAA6C;IAC7C,kBAAkB;IAClB,kBAAkB;IAClB,OAAO;IACP,mCAAmC;;AAEvC;;AAEA;;IAEI,4CAA4C;IAC5C,6BAA6B;IAC7B,kBAAkB;IAClB,eAAe;IACf,WAAW;IACX,SAAS;AACb;;AAEA;;IAEI,YAAY;IACZ,sCAAsC;IACtC,kBAAkB;IAClB,0BAA0B;IAC1B,uBAAuB;IACvB,sBAAsB;IACtB,qBAAqB;IACrB,kBAAkB;IAClB,UAAU;IACV,eAAe;;AAEnB;;AAEA;;IAEI,8CAA8C;AAClD;AACA,WAAW;;AAEX;;IAEI,uBAAuB;IACvB,6CAA6C;IAC7C,6CAA6C;IAC7C,UAAU;IACV,mCAAmC;IACnC,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,eAAe;IACf,8BAA8B;;IAE9B,wBAAwB;AAC5B;AACA;IACI,mCAAmC;AACvC;;AAEA;IACI,aAAa;IACb,mCAAmC;IACnC,mBAAmB;AACvB;;AAEA;;IAEI,mCAAmC;IACnC,wBAAwB;AAC5B","sourcesContent":["\n\n*{\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n    color: var(--theme-contrast-color);\n}\n\n:root\n{\n    --theme-primary-color: #EDF0F0;\n    --theme-secondary-color: #ffffff;\n    --theme-contrast-color: #000000;\n    --theme-substitue-color: #c3c6d1;\n    --theme-contrast-dull: #2a2b2b;\n    --hero-font: \"quicksand\";\n    --content-font: \"cantarell\";\n    --font-resizer: calc(0.5rem + 0.6vw + 0.2vh);\n    --blend-color: #c3c6d113;\n}\n\nbody{\n    background-color: var(--theme-primary-color);\n}\n\nimg\n{\n    max-width: 100%;\n    height: auto;\n    width: auto\\9; /* ie8 */\n}\n\n#main-header\n{\n    /* height: 50px; */\n    /* height: 80px; */\n    height: clamp(40px, 10vh, 80px);\n    width: 100vw;\n    background-color: var(--theme-secondary-color);\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    /* padding: 0 clamp(30px, 10%,100px); */\n    padding: calc(0.5rem + var(--font-resizer));\n    box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.11);\n \n}\n\n.logo\n{\n    display: flex;\n    height: 100%;\n    width: fit-content;\n    justify-content: center;\n    align-items: center;\n    font-size: calc(0.8rem + 1.4vw + 0.3vh);\n    font-family: var(--hero-font), 'Times New Roman', Times, serif, sans-serif;\n}\n.n-logo\n{\n   height: calc(3.99rem + 0.9vw + 0.6vh);\n   min-height: 1rem;\n   max-height: 100px;\n   width: auto;\n}\n/* No avatar element in the page yet */\n.avatar\n{\n    height: calc(1.5rem + 0.6vw + 0.2vh);\n    width: calc(1.5rem + 0.6vw + 0.2vh);\n    display: inline-block;\n    border-radius: 50%;\n    -webkit-border-radius: 50%;\n    -moz-border-radius: 50%;\n    -ms-border-radius: 50%;\n    -o-border-radius: 50%;\n    background-color: var(--theme-secondary-color);\n}\n\n.corner-profile\n{\n    /* background-color: rgba(137, 43, 226, 0.267); */\n    display: flex;\n    list-style-type: none;\n    gap: 0.5em;\n}\n\n.corner-profile .info-btn\n{\n    background-color: transparent;\n    border: none;\n    padding: auto;\n}\n.corner-profile .info-btn i\n{\n    font-size: calc(1.3rem + 0.6vw + 0.2vh);\n}\n\n.corner-profile li\n{\n    padding: 0 1vw;\n}\n\n/* Main styling */\n\n#container\n{\n    font-family: var(--content-font), sans-serif;\n    display: grid;\n    grid-template-columns: 1fr 4fr;\n    grid-template-rows: 0fr auto;\n    height: calc(100vh - (clamp(40px, 10vh, 80px) + clamp(20px, 5vh, 40px)));\n    overflow: auto;\n}\n\n/* Nav bar */\n#navbar-container\n{\n    grid-column: 2/5;\n    grid-row: 1/1;\n}\n#list-navigator\n{\n    height: clamp(20px, 5vh, 60px);\n    width: 100%;\n    /* background-color: aliceblue; */\n    box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.11);\n    margin-top: 2px\n}\n\n#burger-icon\n{\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: flex-start;\n    height: 100%;\n    width: 100%;\n    min-width: 2rem;\n    max-width: 100px;\n    /* background-color: black; */\n}\n\n#burger-icon .line\n{\n    background-color: var(--theme-contrast-color);\n    width: 80%;\n    height: 3px;   \n    border-radius: 4px;\n    -webkit-border-radius: 4px;\n    -moz-border-radius: 4px;\n    -ms-border-radius: 4px;\n    -o-border-radius: 4px;\n    margin: 2px 0;\n}\n#burger-icon .line1\n{\n    width: 80%;\n}#burger-icon .line2\n{\n    width: 60%;\n}#burger-icon .line3\n{\n    width: 40%;\n}\n\n\n#list-navigator ul\n{\n    list-style-type: none;\n    display: flex;\n    gap: max(3px, 0.3vw);\n    justify-content: flex-start;\n    align-items: center;\n    height: 100%;\n    /* overflow-x: scroll; */\n    overflow-x: auto;\n    /* scrollbar-width: none; */\n}\n/* #list-navigator ul::-webkit-scrollbar\n{\n    display: none;\n} */\n#list-navigator ul li>button\n{\n    background-color: transparent;\n    border: none;\n    font-size: calc(0.5rem + 0.6vw + 0.2vh);\n    height: 100%;\n    width: 100%;\n\n}\n#list-navigator ul li\n{\n    position: relative;\n    height: 100%;\n    width: max-content;\n    max-width: fit-content;\n    min-width: fit-content;\n    padding: 0 3%;\n    border-radius: 4px;\n    -webkit-border-radius: 4px;\n    -moz-border-radius: 4px;\n    -ms-border-radius: 4px;\n    -o-border-radius: 4px;\n}\n#list-navigator ul li::after\n{\n    content: \" \";\n    height: 60%;\n    width: 2px;\n    background-color: var(--theme-contrast-color);\n    position: absolute;\n    top: 20%;\n    right: 0;\n    border-radius: 5px;\n    -webkit-border-radius: 5px;\n    -moz-border-radius: 5px;\n    -ms-border-radius: 5px;\n    -o-border-radius: 5px;\n    opacity: 0.4;\n}\n#list-navigator ul li:last-child\n{\n    opacity: 0.3;\n}\n#list-navigator ul li:hover\n{\n    background-color: var(--theme-substitue-color);\n    transition: all 0.3s ease-out;\n    -webkit-transition: all 0.3s ease-out;\n    -moz-transition: all 0.3s ease-out;\n    -ms-transition: all 0.3s ease-out;\n    -o-transition: all 0.3s ease-out;\n}\n\n/* aside-section */\n#side-column\n{\n    /*grid-area: side-menu;\n    */background-color: var(--theme-primary-color);\n    grid-column: 1/2;\n    grid-row: 1/5;\n    padding: calc(0.5rem + var(--font-resizer));\n    box-shadow: 7px 2px 3px rgba(0, 0, 0, 0.11);\n    z-index: 2;\n    overflow-y: auto;\n}\n.general-heading\n{\n    margin: max(2px, 0.3vw) max(4px, 2vw);\n    font-size: calc(0.6rem + var(--font-resizer));\n}\n#all-projects\n{\n    list-style-type: none;\n    display: flex;\n    flex-direction: column;\n    gap: 4px;\n    justify-content: flex-start;\n    align-items: center;\n    /* border: 1px solid salmon; */\n    margin-top: 10%;\n    font-weight: bolder;\n    padding: calc(var(--font-resizer));\n\n}\n\n#all-projects li\n{\n    min-width: fit-content;\n    width: max(100px, 80%);\n    background-color: var(--theme-contrast-dull);\n    height: clamp(20px, 5vh, 60px);\n    border-radius: 4px;\n    -webkit-border-radius: 4px;\n    -moz-border-radius: 4px;\n    -ms-border-radius: 4px;\n    -o-border-radius: 4px;\n    position: relative;\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    gap: 4px;\n    padding: calc(var(--font-resizer));\n}\n\n#all-projects li button\n{\n    min-width: fit-content;\n    width: 100%;\n    background-color: transparent;\n    border: none;\n    font-size: calc(0.2rem + var(--font-resizer));\n    min-height: 100%;\n    color: var(--theme-secondary-color);\n}\n.fa-trash\n{\n    /*content: \"X\";\n    */color: salmon;\n    font-size: var(--font-resizer);\n    \n    margin-right: 3%;\n    opacity: 1;\n    background-color: #c3c6d113;\n    padding: clamp(3px, 4%, 6px);\n    border-radius: 6px;\n    -webkit-border-radius: 6px;\n    -moz-border-radius: 6px;\n    -ms-border-radius: 6px;\n    -o-border-radius: 6px;\n}\nbutton[del-list-index], button[del-project-index]\n{\n    border: none;\n}\nbutton[del-list-index]:hover\n{\n    background-color: var(--theme-contrast-dull);\n    border-radius: 4px;\n    -webkit-border-radius: 4px;\n    -moz-border-radius: 4px;\n    -ms-border-radius: 4px;\n    -o-border-radius: 4px;\n}\n#all-projects li:hover .fa-trash\n{\n    background-color: var(--theme-contrast-color);\n    opacity: 1;\n}\n\n#all-projects li:last-child\n{\n    opacity: 0.3;\n}\n#all-projects li:hover\n{\n    background-color: var(--theme-contrast-color);\n    transition: all 0.3s ease-in-out;\n    -webkit-transition: all 0.3s ease-in-out;\n    -moz-transition: all 0.3s ease-in-out;\n    -ms-transition: all 0.3s ease-in-out;\n    -o-transition: all 0.3s ease-in-out;\n}\n\n\n/* list area */\n\n#main-area\n{\n    /* grid-area: main; */\n    background-color: var(--theme-secondary-color);\n    grid-column: 2/5;\n    grid-row: 2/5;\n}\n\n#main-lists-area\n{\n    padding: max(5px, 1.7vw);\n}\n\n#main-lists-area hr\n{\n    background-color: var(--theme-contrast-dull);\n    height: 1px;\n    width: 60%;\n    border-radius: 2px;\n    border: none;\n    -webkit-border-radius: 2px;\n    -moz-border-radius: 2px;\n    -ms-border-radius: 2px;\n    -o-border-radius: 2px;\n    opacity: 0.6;\n}\n\n.date-and-time\n{\n    width: 50vw;\n}\n.date-and-time #date, .date-and-time #time\n{\n    margin: 0 5%;\n    font-size: var(--font-resizer);\n}\n\n#list-container\n{\n    height: fit-content;\n    width: fit-content;\n    max-width: 100%;\n    min-width: clamp(100px, 45%, 250px);\n    border: 1px outset var(--theme-contrast-color);\n    border-radius: 4px;\n    -webkit-border-radius: 4px;\n    -moz-border-radius: 4px;\n    -ms-border-radius: 4px;\n    -o-border-radius: 4px;\n    margin: 3% 0;\n    position: relative;\n    padding: max(20px, 3%) 3%;\n    box-shadow: 3px 3px var(--theme-contrast-dull);\n}\n#list-container::before\n{\n    content: \"....\";\n    font-family: monospace;\n    background-color: var(--theme-contrast-dull);\n    color: var(--theme-substitue-color);\n    border-radius: 3px 3px 0 0 ;\n    -webkit-border-radius: 3px 3px 0 0 ;\n    -moz-border-radius: 3px 3px 0 0 ;\n    -ms-border-radius: 3px 3px 0 0 ;\n    -o-border-radius: 3px 3px 0 0 ;\n    width: 100%;\n    position: absolute;\n    text-align: center;\n    height: fit-content;\n    top: 0;\n    left: 0;\n   right: 0;\n}\n\n#list dt\n{\n    font-weight: bold;\n    position: relative;\n    font-size: calc(0.2rem + var(--font-resizer));\n    padding-left: calc(clamp(10px, 2vw, 15px)  + 0.5rem);\n    width: 90%;\n    word-wrap: break-word;\n}\n#list dt span\n{\n    height: clamp(10px, 2vw, 15px);\n    width: clamp(10px, 2vw, 15px);\n    border: 2px solid;\n    \n    border-radius: 50%;\n    position: absolute;\n    left: 0;\n    top: calc(clamp(10px, 2vw, 15px)/2);\n}\n#list dt span:hover\n{\n    background-color: var(--theme-substitue-color);\n    background-color: var(--priority-color);\n}\n#list dd\n{\n    margin: 3% clamp(30px, 5vw, 40px);\n    position: relative;\n    padding-left: calc(clamp(10px, 2vw, 15px)  + 0.2rem);\n     font-size: calc(0.04rem + var(--font-resizer));\n    width: 80%;\n    word-wrap: break-word;\n    min-height: clamp(10px, 3vh, 50px);\n    display: flex;\n    flex-direction: column;\n}\n\n#list dd::before\n{\n    content: \" \";\n    height: clamp(5px, 1.2vw, 10px);\n    width: clamp(5px, 1.2vw, 10px);\n    background-color: var(--theme-contrast-color);\n    border-radius: 50%;\n    position: absolute;\n    left: 0;\n    top: calc(clamp(10px, 2vw, 15px)/2);\n    \n}\n\n#list dd span:last-child\n{\n    font-size: calc(var(--font-resizer) - 0.2em);\n    font-family: var(--hero-font);\n    position: relative;\n    margin-top: 10%;\n    right: -70%;\n    bottom: 0;\n}\n\n#add-task\n{\n    opacity: 0.5;\n    border: 1px solid rgba(0, 0, 0, 0.418);\n    border-radius: 4px;\n    -webkit-border-radius: 4px;\n    -moz-border-radius: 4px;\n    -ms-border-radius: 4px;\n    -o-border-radius: 4px;\n    text-align: center;\n    padding: 0;\n    margin-top: 5vh;\n  \n}\n\n#add-task:hover\n{\n    background-color: var(--theme-substitue-color);\n}\n/* footer */\n\n#universal-footer\n{\n    /* grid-area: footer; */\n    background-color: var(--theme-contrast-color);\n    box-shadow: 0px -4px 4px rgba(0, 0, 0, 0.404);\n    z-index: 4;\n    color: var(--theme-substitue-color);\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    p